.experience {
    min-height: 100vh;
}

.experience-body {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding-top:5rem;
}

.experience-description {
    flex: 0.65;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    margin-right: 2rem;
}

.experience-description > h1 {
    font-size: 3.5rem;
    font-family: var(--primaryFont);
    margin-bottom: 2rem;
}

.experience-card {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    width: 90%;
    height: 140px;
    padding: 1.5rem;
    border-radius: 20px;
    margin-bottom: 1.5rem;
    transition: background-color 200ms ease-in-out;
}

.expcard-img {
    border-radius: 50%;
    width: 55px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

.expcard-img > img {
    width: 36px;
    pointer-events: none;
}

.experience-details {
    margin-left: 0.6rem;
}

.experience-details > h6 {
    font-family: var(--primaryFont);
    font-size: 0.85rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
}

.experience-details > h4,
.experience-details > h5 {
    font-family: var(--primaryFont);
    font-size: 1.225rem;
    font-weight: 600;
}

.experience-image {
    box-sizing: border-box;
    padding: 1rem;
    flex: 0.35;
    margin-left: 7%;
    margin-top: 1rem;
    pointer-events: none;
}

.experience-image > img {
    width: 100%;
    pointer-events: none;
}

/* Media Queries */

@media (max-width: 1380px) {
    /* You can add responsive adjustments here if needed */
}

@media (max-width: 992px) {
    .experience {
        min-height: auto;
    }
    .experience-image {
        display: none;
    }
    .experience-description {
        flex: 1;
        margin: auto;
        padding: 1rem;
    }
    .experience-description > h1 {
        font-size: 3.2rem;
    }
    .experience-card {
        width: 100%;
        padding: 1rem;
        height: auto;
    }
}

@media (max-width: 800px) {
    .experience-description > h1 {
        font-size: 3rem;
        margin-bottom: 2rem;
        align-self: center;
    }
    .experience-card {
        padding: 1rem;
        height: 130px;
    }
    .expcard-img {
        width: 45px;
        height: 45px;
    }
    .expcard-img > img {
        width: 30px;
    }
    .experience-details > h6 {
        font-size: 0.85rem;
        margin-bottom: 0.45rem;
    }
    .experience-details > h4,
    .experience-details > h5 {
        font-size: 1.125rem;
        line-height: 126%;
    }
}

@media (max-width: 600px) {
    .experience-description > h1 {
        font-size: 2.5rem;
    }
}

/* You can add more media queries for smaller resolutions if needed */
