  font-weight: 600;
  font-size: 3.25rem;
  line-height: 110%;
  margin: 1rem 0;
}

.lcr--content p {
  margin-top: 1.45rem;
  font-weight: 500;
  font-size: 1.15rem;
  opacity: 0.7;
}

.lcr-buttonContainer {
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

@media (max-width: 1100px) {
  .landing--img {
    --img-size: 350px;
  }
