.slider {
  position: relative;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Nunito", sans-serif;
  height: 60vh; /* Adjust as needed */
}

.left-arrow{
    position: absolute;
  top: 50%;
  font-size: 3rem;
  color: #029d71;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 10;
  pointer-events: auto;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  transition: color 0.3s ease;
}

.right-arrow {
  position: absolute;
  top: 50%;
  font-size: 3rem;
  color: #029d71;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 10;
  pointer-events: auto;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  transition: color 0.3s ease;
}


.left-arrow:hover,
.right-arrow:hover {
  color: #02643d;
}

.left-arrow {
  left: 1rem;
}

.right-arrow {
  right: 1rem;
}

#slide-element {
  width: 60vw;
  border-radius: 1rem;
  -webkit-user-drag: none;
  user-drag: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: auto;
}

.slide {
  opacity: 0;
  transition: opacity 1s ease, -webkit-transform 1s ease;
  transition: opacity 1s ease, transform 1s ease;
  transition: opacity 1s ease, transform 1s ease, -webkit-transform 1s ease;
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide.active {
  opacity: 1;
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
  position: relative;
}

@media screen and (max-width: 1024px) {
  .left-arrow,
  .right-arrow {
    display: none;
  }

  #slide-element {
    width: 80vw;
    border-radius: 0.5rem;
  }
}

