.item {
    border-radius: 15px;
    padding: 3rem;
    font-size: 1.75rem;
    text-align: center;
    font-family: sans-serif;
    color: #fff;
    font-family: var(--primaryFont);
    font-weight: bold;
    letter-spacing: .1rem;
    transition: 300ms ease-in-out;
    height: 11rem;
    
    &--1 {
      background-image: url('../../assets/png/links/Figma.jpg');
  
    }
    
    &--1:hover  {
      transform: scale(1.15)
    }
    
    &--2 {
      background-image: url('../../assets/png/links/Presentation.png');
      color: #000;
    }
    
    &--2:hover  {
      transform: scale(1.15)
    }
    
    &--3 {
      background-color: blueviolet;
    }
  }